@import url(https://fonts.googleapis.com/css2?family=Cardo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
    text-align: center;
}

/* NAVBAR */

.navbar {
    background: lightgray;
    box-shadow: 0 2px 2px -2px rgba(0,0,0,1);
    text-align: left;
}

.mr-auto {
    font-family: 'Cardo', serif;
}

/* HEADER */

.header {
    background-image: url(/my-portfolio/static/media/background.aa915f8e.jpg);
    /* margin-top: 55px; */
    padding-top: 40px;
    height: 175px;
}

/* HEADINGS */

h1 {
    font-family: 'Cardo', serif;
}
 
h3 {
    font-family: 'Cardo', serif;
}

h4 {
    font-family: 'Cardo', serif;
    margin-top: 25px;
}

h6 {
    margin-top: 15px;
}

/* ABOUT */

.about {
    max-width: 100%;
    margin-left: 25%;
    margin-right: 25%;
    border-top: 1px solid lightgray;
    padding-top: 1rem;
    height: 30%;
}

.pic {
    width: auto;
    max-width: 250px;
    max-height: 250px;
    padding: 10px;
}

/* PROJECTS */

.projects {
    border-top: 1px solid lightgray;
    padding-top: 1rem;
    height: 30%;
}

.project-gallery {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    padding-bottom: 20px;
}

.card-container {
    padding: 15px;
}

/* RESUME */

.resume {
    border-top: 1px solid lightgray;
    padding-top: 1rem;
    height: 30%;
    padding-bottom: 20px;
}

.resume-buttons {
    display: inline-block;
    margin: 10px 1rem;
}

/* CONTACT */

.contact {
    border-top: 1px solid lightgray;
    padding-top: 1rem;
    height: 30%;
}

.social-media a {
    display: inline-block;
    margin: 10px 1rem;
    font-size: 2.5rem;
    color: black;
}

/* FOOTER */

.footer {
    padding-bottom: 20px;
}
